if (modelType === 'single') {
  isLocalPC()
} else if (modelType === 'colonyBox') {
  isBox()
}
onMounted(() => {
  nextTick(() => {
    cpuChart = useEcharts(cpuRef.value, 'dark').myChart
    memoryChart = useEcharts(memoryRef.value, 'dark').myChart
    window.onresize = () => {
      cpuChart.resize()
      memoryChart.resize()
    }
  })
})
onUnmounted(() => {
  window.removeEventListener('message', messageChange)
  if (modelType === 'single') {
    vscode.postMessage({
      type: 'kill',
      pid: swpanPid
    })
  } else if (modelType === 'colonyBox') {
    socket.close()
  }
})

